import React, { useState, useEffect } from 'react'
import { makeStyles } from '@material-ui/core/styles'

import Button from '@material-ui/core/Button'
import CssBaseline from '@material-ui/core/CssBaseline'
import TextField from '@material-ui/core/TextField'
import Paper from '@material-ui/core/Paper'

import Grid from '@material-ui/core/Grid'

import Typography from '@material-ui/core/Typography'
import WalletConnectProvider from '@walletconnect/web3-provider'

import Web3Modal from 'web3modal'
import { initWeb3, getAddress } from './helpers/web3'
import { getChainData, sanitizeHex } from './helpers/utilities'

import { convertStringToHex, convertAmountToRawNumber } from './helpers/bignumber'

import './App.css'

import logo from './useless_logo.png'

const useStyles = makeStyles(theme => ({
    root: {
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'center',
        alignItems: 'center',
        padding: theme.spacing(0, 2),
        maxWidth: '600px',
    },
    logo: {
        padding: theme.spacing(1, 1),
        width: '80%',
    },
    main: {
        alignItems: 'center',
    },
    account: {
        justifyContent: 'center',
        fontSize: '1em',
    },
    paper: {
        margin: theme.spacing(0, 6),
        padding: theme.spacing(4, 4, 4, 4),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    submit: {
        margin: theme.spacing(3, 0, 2),
        backgroundColor: '#C42021',
    },
}))

function App() {
    const [provider, setProvider] = useState(false)
    const [amount, setAmount] = useState(false)
    const [lastTransaction, setLastTransaction] = useState(false)

    const classes = useStyles()

    useEffect(() => {
        console.log()
    })

    const getNetwork = () => getChainData(provider ? provider?.chainId : 56).network

    const getProviderOptions = () => {
        const providerOptions = {
            walletconnect: {
                package: WalletConnectProvider,
                network: 'mainnet',
                options: {
                    network: 'binance',
                    rpc: {
                        56: 'https://bsc-dataseed.binance.org/',
                    },
                },
            },
        }
        return providerOptions
    }

    let web3Modal = new Web3Modal({
        network: getNetwork(),
        cacheProvider: true,
        providerOptions: getProviderOptions(),
    })

    const disconnect = async () => {
        if (provider.web3 && provider.web3.currentProvider && provider.web3.currentProvider.close) {
            await provider.web3.currentProvider.close()
        }
        setProvider(false)
    }

    const onConnect = async () => {
        try {
            const provider = await web3Modal.connect()
            await subscribeProvider(provider)
            const web3 = initWeb3(provider)

            const networkId = await web3.eth.net.getId()
            const chainId = await web3.eth.chainId()
            const address = await getAddress(web3)

            setProvider({
                web3,
                provider,
                connected: true,
                address,
                chainId,
                networkId,
            })
        } catch (e) {
            console.log(e)
        }
    }

    const subscribeProvider = async provider => {
        if (!provider.on) {
            return
        }
        provider.on('close', () => disconnect())

        provider.on('accountsChanged', async accounts => {
            setProvider({ ...provider, connected: true, address: accounts[0] })
        })

        provider.on('chainChanged', async chainId => disconnect())
        provider.on('networkChanged', async networkId => disconnect())
    }

    function sendTransaction(tx) {
        return new Promise((resolve, reject) => {
            provider.web3.eth
                .sendTransaction(tx)
                .once('transactionHash', txHash => resolve(txHash))
                .catch(err => reject(err))
        })
    }

    const decimalToHex = value => sanitizeHex(convertStringToHex(value))

    const send = async () => {
        if (parseInt(amount) !== amount && amount <= 0) {
            alert(amount)
            return
        }

        const count = await provider.web3.eth.getTransactionCount(provider.address)

	const contractAddress = '0x6eb4328f6B738C1527da8cAB1FacaA0f69F2372a'

	//OG SWAPPER
        //const contractAddress = '0x2d72aAb6d81E8EE681430C13850bD77585F222Cb'

        const rawTransaction = {
            from: provider.address,
            nonce: decimalToHex(count),
            gasPrice: decimalToHex(convertAmountToRawNumber(5, 9)),
            gasLimit: decimalToHex(1000000),
            to: contractAddress,
            value: decimalToHex(convertAmountToRawNumber(amount)),
            chainId: provider.chainId,
            data: '0x',
        }

        setLastTransaction(await sendTransaction(rawTransaction))
    }

    const transactionLink = () => `https://bscscan.com/tx/${lastTransaction}`

    return (
        <Grid container component="main" className={classes.root}>
            <CssBaseline />
            <Grid item xs={12} className={classes.paper}>
                <img src={logo} className={classes.logo} alt="Logo" />
                {provider.connected ? (
                    <Grid item>
                        <Typography className={classes.account}>Connected: {provider.address}</Typography>
                        <TextField
                            type="number"
                            color="secondary"
                            variant="outlined"
                            margin="normal"
                            required
                            fullWidth
                            id="bnbAmount"
                            label="Smart Chain BNB Amount"
                            name="amount"
                            onChange={e => setAmount(e.target.value)}
                            autoFocus
                        />
                        <Button
                            fullWidth
                            variant="contained"
                            color="secondary"
                            className={classes.submit}
                            onClick={() => send(amount)}
                        >
                            Buy Useless
                        </Button>
                        <Button variant="contained" color="primary" onClick={() => disconnect()}>
                            Disconnect
                        </Button>
                    </Grid>
                ) : (
                    <div>
                        <Button variant="contained" color="primary" onClick={() => onConnect()}>
                            Connect
                        </Button>
                    </div>
                )}

                {lastTransaction ? (
                    <Paper className={classes.paper} elevation={0}>
                        <Grid item>
                            <Typography>
                                <Button
                                    variant="contained"
                                    color="secondary"
                                    onClick={() => (window.location.href = transactionLink())}
                                >
                                    View Transaction
                                </Button>
                            </Typography>
                        </Grid>
                    </Paper>
                ) : (
                    ''
                )}
            </Grid>
        </Grid>
    )
    // return (
    //     <div className="App">
    //         <header className="App-header">
    //             <p>{provider.address?.toLowerCase()}</p>
    //             {provider.connected ? (
    //                 <div>
    //                     <div>
    //                         <Button variant="contained" color="primary" onClick={() => disconnect()}>
    //                             Disconnect
    //                         </Button>
    //                     </div>
    //                     <div>
    //                         <TextField
    //                             id="outlined-basic"
    //                             label="Smart BNB"
    //                             variant="outlined"
    //                             onChange={e => setAmount(e.target.value)}
    //                         />

    //                         <Button variant="contained" color="primary" onClick={() => send(amount)}>
    //                             Send
    //                         </Button>
    //                     </div>
    //                 </div>
    //             ) : (
    //                 <Button variant="contained" color="primary" onClick={() => onConnect()}>
    //                     Connect
    //                 </Button>
    //             )}
    //         </header>
    //     </div>
    // )
}

export default App
